@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;700&display=swap);

  #logo-responsivo{
    display: none;
}
@media (max-width: 900px){
    #logo-responsivo{
        display: block;
    }

    #logo-responsivo {
        display: flex;
        position: fixed;
        margin-left: -15px;
        opacity: 0.8;
    }
    #logo-responsivo {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-end;
    }


    #logo-responsivo img{
        width: 15%;
        height: 25%;
        display: flex;
        align-items: flex-end;
    }
    #libra-responsivo{
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        margin-right: 10px;
        margin-top: 1px;
    }
    #libra-responsivo a{
        color: rgb(189, 0, 0);
        font-size: 13px;
    }
    #libra-responsivo img{
        height: 20px;
        width: 20px;
        margin-right: 2px;
    }

}
#logo-menu {
    display: flex;
    justify-content: center;
}
#logo-menu {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}


#logo-menu img{
    width: 45%;
    height: 45%;
    margin-bottom: 53px;
}
#menu{
    width: 220px;
    height: 100vh;
    background-color: #29275f;
    display: flex;
    flex-direction: column;
    border-radius: 0px 8px 8px 0px;
    position: fixed;
}

#responsivo{
    display: none;
}

#menu h1{
    color: #ffffff;
    font-size: 20px;
    padding: 60px 20px;
}

#menu nav ul li{
    margin-bottom: 70px;
    padding: 0 8px;
    color: #ffffff;
}

#menu .fas{
    width: 40px;
    height: 20px;
}

#menu nav ul li a{
    display: flex;
    color: #e9e9f0;
    padding-top: 30px;
    font-size: 15px;
    padding-left: 10px;
}

#back-menu:hover{
    background-color: rgb(0, 0, 0);
    height: 60px;
    border-radius: 8px;
}

#menu nav ul li a:hover{
    color: #ffffff;
    text-decoration: underline; 
}

.logo{
    width: 200px;
    margin-left: 10px;
    margin-top: 20px;
}
#libra{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-right: 10px;
    margin-top: 10px;
}
#libra a{
    color: rgb(219, 0, 0);
    font-size: 10px;
}
#libra img{
    height: 20px;
    width: 20px;
    margin-right: 2px;
}

@media (max-width:900px){
    #menu{
        display: none;
    }

    #responsivo{
        width: 100%;
        height: 100%;
        position: fixed;
        top: 90%;
        z-index: 200;
        display: flex;
        align-content: center;
        justify-content: center;
        background-color: #29275f;
        opacity: 0.9;
    }

    #responsivo ul {
        width: 100%;
        height: 58px;
        text-align:center;
        display: flex;
        justify-content: space-around;
    }
    #responsivo li{
        list-style: none;
        float: left;
        width: 100%;
    }
    #responsivo .fas{
        color: white;
        font-size: 70px;
        position: relative;
        padding: 1px 0;
        justify-content: space-between;
        width: 40%;
        top: -15%;
    }
   
}

#rodape{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 80px;
    background-color: #29275f;
}
#rodape1{
    align-items: center;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 60%;
    background-color: #29275f; 
}
#rodape2{
    align-items: center;
    display: flex;
    flex-direction: column;
    width: 100%;
    height:40%;
    background-color: #151436; 
}
#rodape1 p{
    color: white;
    font-size: 13px;
    margin-left: 225px;
}
#rodape2 p{
    color: white;
    font-size: 13px;
    margin-left: 225px;
}
.rcont{
    color: white;
    font-size: 14px;
    margin-left: 225px;
}
.rcont:hover{
   text-decoration: underline;
}
#rodape2 p{
    font-size: 10px;
}
#page-home, #pagecontato, #page-data, #pagesobre{
    flex-direction: column;
}

@media (max-width:900px){
    #rodape1 p{
       
        margin-left: 0px;
    }
    #rodape2 p{
       
        margin-left: 0px;
    }
    .rcont{
        margin-left: 0px;
    }
    #rodape{
        margin-bottom: 60px;
    }
   
}
#page-home{
    display: flex;
    position: relative;
    justify-content: space-between;
}

#main-content{
    background-color: #f3f0f5;
    padding: 30px;
    padding-left: 20%;
    height: 100%;    
}

#main-content h3{
    color: #29275f; 
    font-family: 'Poppins', sans-serif;
}

#main-content .header{ 
    width: 100%;
