    border-bottom: 1px solid #e0e4e8;
    border-radius: 8px;
}

#header-img img{
    border-radius: 8px;
}

#header-img-responsivo{
    display: none;
}


#main-content .info{
    width: 100%;
    height: 80vh;
    display: flex;
    justify-content: space-between;
    margin-top: 35px;
    text-align: center;
    color: #000000;
}

#main-content .info div{
    width: 45%;
    height: 79vh;
    background: #ffffff;
    margin: 0px;
    color: #000000;
    box-shadow: 3px 3px 3px rgba(87, 85, 85, 0.77);;
    border-radius: 8px;
    text-align: left;
    font-size: 15px;
}

#main-content .info2{
    width: 100%;
    height: 80vh;
    display: flex;
    justify-content: space-between;
    margin-top: 35px;
    text-align: center;
    color: #000000;
}

#main-content .info2 div{
    width: 45%;
    height: 79vh;
    background: #ffffff;
    margin: 0px;
    color: #000000;
    box-shadow: 3px 3px 3px rgba(87, 85, 85, 0.77);;
    border-radius: 8px;
    text-align: left;
    font-size: 15px;
}
.text-card{
    text-indent: 20px;
}

.info h3{
    text-align: center;
    margin-top: 25px;
}

.info h4{
    text-align: center;
    color: #20275f;
    margin-top: 12px;
}
.info li{
    margin: 40px;
}
.info p{
    margin: 40px;
    text-align: justify;
    line-height: 4vh;
}

.info2 h3{
    text-align: center;
    margin-top: 25px;
}

.info2 h4{
    text-align: center;
    color: #20275f;
    margin-top: 12px;
}
.info2 li{
    margin: 40px;
}
.info2 p{
    margin: 40px;
    text-align: justify;
    line-height: 4vh;
}

.source{
    font-size: 12px;
}

.source a:visited{
    color: #000000;
}

#barra{
    overflow: auto;
    background-color: #20275f;
}

@media (max-width: 900px){
    #main-content {
        background-color: #f3f0f5;
        padding: 12px;
        height: 100%;
    }
    #main-content .header{
        width: 100%;
        height: 166px;
        border-radius: 10px;
        box-shadow: 10px 21px 39px rgb(15, 14, 39);
    }

    #header-img {
        display: none;
    }

    #header-img-responsivo{
        display: block;
    }
    #header-img-responsivo img{
        width: 100%;
        height: 166px;
        border-radius: 10px;
    }

    #main-content .info{
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
    }
    #main-content .info div{
        width: 99%;
        height: 79vh;
        margin: 3px;
        box-shadow: 3px 3px 33px #8f8cde;
        border-radius: 34px;
        margin-top: 10px;
    }

    #main-content .info2{
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        margin: auto;
        margin-top: 2vh;
    }
    #main-content .info2 div{
        width: 99%;
        height: 550px;
        margin: 3px;
        margin-top: 10px;
        box-shadow: 3px 3px 33px #8f8cde;
        border-radius: 34px;
    }
   
}

.main-confirmed  div{
    display: flex;
    justify-content: space-between;
    padding: 10px;
    margin-left: 25px;
    margin-right: 25px;
}
#page-data{
    display: flex;
    position: relative;
    flex:1 1;
    background-color: #29275f;
}

#main-cards{
    background-color: #f3f0f5;
    padding: 40px;
    padding-left: 20%;
    width: 100%;
    height: 100vw;    
}

#main-cards h1{
    font-size: 22px;
    text-align: center;
    padding-bottom: 10px;
    color: #20275f;
    margin-top: 25px;
}


#cities{
    width: 100%;
    padding: 20px;
    margin-top: 1px;
}
#cities a{
    align-items: center;
    justify-content: center;
    color: #0e0c41;
}

#cities h2{
    color: #20275f;
    font-size: 20px;
    text-align: center;
}



#cities p{
    padding-top: 10px;
    color: #20275f;
    font-size: 13px;
    margin: 0;
    text-align: center;
}

#cities a:hover{
    text-decoration: underline;
}

#dataAtual{
    display: flex;
    flex-direction: column;
    margin-top: 40px;
    align-items: flex-end;
    color: #000000;
    font-size: 13px;
}

#card-gerais{
    width: 100%;
    height: 140px;
    display: flex;
    justify-content: space-between;
    margin-top: 35px;
    text-align: center;

}

.card-filhos{
    margin: 10px;
    width: 15%;
    height: 120px;
    border-radius: 8px;
}

.card-filhos div{
    height: 100px;
    border-radius: 8px;
}
#card-suspeitos{
    background-color: rgba(255, 172, 64, 0.753);
    box-shadow: 3px 3px 3px rgb(233, 156, 55);
}

.paragrafo-data p{
    font-size: 50px;
    color: #f3f0f5;
    text-align: center;
    padding-top: 5px;
}
#card-gerais h3{
    margin: 10px;
    color: #000000;
    text-align: center;
}

#card-confirmados{
    background-color: rgba(246, 75, 84, 0.781);
    box-shadow: 3px 3px 3px rgb(204, 50, 57);
}

#card-recuperados{
    background-color: rgba(29, 233, 182, 0.733);
    box-shadow: 3px 3px 3px rgb(34, 194, 154);
}


#card-obitos{
    background-color: rgba(41, 39, 95, 0.822);
    box-shadow: 3px 3px 3px rgb(15, 14, 39);
}

#card-confirmado {
    background-color: #ffffff;
    margin-left: 240px;
    width: 58%;
    margin-top: 80px;
    height: 400px;
    box-shadow: 2px 2px 2px rgba(87, 85, 85, 0.77);
    border-radius: 8px;
}

#card-confirmado  h2{
    padding: 20px;
    margin: 30px 20px 0px;
    color: #20275f;
    text-align: center;
    font-size: 20px;
}

#card-confirmado {
    overflow: auto;
}

@media(max-width: 900px){
    #page-data{
        margin: auto;
        width: 100%;
    }
    #main-cards{
        padding: 0px;
        padding-left: 0%;
        width: 100%;
        margin: auto;
        height: 800px;
    }

    #main-cards h1{
        margin: auto;
        margin-top: 15px;
    }

    #cities p{
        margin: auto;
        width: 76%;
    }
    #dataAtual{
        align-items: flex-end;
        margin-top: 20px;
        margin: 9px;
    }

    #dataAtual h2{
        font-size: 15px;
    }

    #dataAtual h3{
        font-size: 12px;
    }

    #card-gerais {
        width: 90%;
        margin: auto;
    }

    #card-gerais p{
        font-size: 22px;
        margin: 6px;
        margin-top: 0px;
    }
    #card-confirmado{
        width: 90%;
        height: 50vh;
        margin: auto;
        box-shadow: 3px 3px 33px #8f8cde;
        border-radius: 34px;
        margin-top: 1px;
        margin-bottom: 7%;
    }
    .card-filhos{
        height: 75px;
    }
    .card-filhos div{
        height: 50px;
        border-radius: 8px;
    }
    .card-filho div{
        font-size: 10px;
        margin: auto;
    }
   
    #title-data{
        display: flex;
        flex-direction: column;
        margin: 10px;
    }
    #title-data h3{
        font-size: 12px;
        margin: 0px;
        -webkit-margin-after: 15px;
                margin-block-end: 15px;
    }
}

#pagecontato{
    display: flex;
    position: relative;
    justify-content: space-between;

}
.App{
    background-color:  #f3f0f5;
}

#maincontato{
    background-color: #f3f0f5;
    padding: 60px;
    padding-left: 22%;
    height: 100vh;
    justify-content: center;
    align-items: center;
    margin-left: 250px;
}

#maincontato h1{
    font-size: 22px;
    text-align: center;
    margin-top: -80px;
    color: #20275f;

}
.cardcontato{
    display: flex;
    flex-direction: column;
    height: 500px;
    width: 500px;
    background-color: white;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    box-shadow: 2px 2px 2px rgba(87, 85, 85, 0.77);

}

.cardcontato ul li{
    list-style: none;
}
.cardcontato ul li a{
    font-size: 18px;
    color: #20275f;
  
}
.cardcontato ul li a:visited{
    color: #20275f;
  
}

.cardcontato ul li a:hover{
    text-decoration: underline;
    color: #0c176e86;
    

}
.fass{
    color: #20275f;
    margin-top: 50px;
    align-items: center;
}

@media (max-width:900px){
    #pagecontato{
        width: 100%;
    }
    #maincontato{
        padding: 0px;
        padding-left: 0%;
        padding-right: 0%;
        margin-left: 0px;
        height: 600px;
        width: 100%;
    }

    #maincontato {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .cardcontato{
        width: 100%;
        height: 500px;
        align-items: center;
        margin: auto;
        border-radius: 34px;
        box-shadow: 3px 3px 33px #8f8cde;
    }
    .cardcontato ul li {
        margin: 12px;
    }
    .cardcontato ul li a{
        font-size: 15px;
    }
    #maincontato h1{
        font-size: 30px;
        margin-bottom: 50px;
    }
}
#pagesobre{
